.login {
    &__side-header {
        background: #130954;
        text-align: center;
        position: relative;
        z-index: 1;
        height: 100vh;
        min-height: 100vh;

        @media only screen and (max-width: 992px) {
            display: none;
        }
    }

    &__overlay {
        background: url("/media/login-form/login-form-bg.png");
        position: absolute;
        background-position: top center;
        background-repeat: no-repeat;
        background-size: cover;
        margin: 0;
        padding: 0;
        height: 100vh;
        min-height: 100vh;
        width: 100%;
        z-index: -1;
    }

    &__side-header-logo {
        position: absolute;
        top: 100px;
    }

    &__side-header-heading {
        color: $primamryColorWhite;
        font-weight: 800;
        font-size: 46px;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 15px;
    }

    &__side-header-description {
        color: $primamryColorWhite;
        font-weight: 400;
        font-size: 16px;
        max-width: 410px;
        margin-left: auto;
        margin-right: auto;
    }

    &__side-header-bottom {
        padding-right: 20px;
        padding-left: 20px;
        padding-bottom: 25px;
        position: absolute;
        bottom: 0;
        width: 100%;
    }

    &__site-header-container {
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        min-height: 100vh;
    }

    &__form-container {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        min-height: 100vh;
    }

    form {
        width: 570px;

        @media only screen and (max-width: 600px) {
            width: 350px;
        }
    }

    &__side-header-bottom-content {
        display: flex;
        justify-content: space-between;
    }

    &__side-header-copyright {
        font-size: 14px;
        font-weight: 400;
        color: $primamryColorWhite;
        margin-top: 3px;

        @media only screen and (max-width: 600px) {
            display: block;
            margin-left: auto;
            margin-right: auto;
        }
    }

    &__side-header-terms-condition {
        text-decoration: none;
        list-style: none;
        color: white;

        &:hover {
            color: white;
        }
    }

    &__form {
        z-index: 2;
    }

    &__mobile-logo {
        width: 200px;
        display: none;
        margin-top: 50px;
        margin-bottom: 50px;

        @media only screen and (max-width: 992px) {
            display: block;
        }
    }

    &__form-header {
        font-size: 35px;
        font-weight: 700;
        text-align: center;
        margin-bottom: 25px;
    }

    &__form-username-input {
        height: 50px;
        border: 2px solid rgba(44, 45, 61, 0.1);

        // font-family: 'Font Awesome 5 Free';
        // font-weight: 400;
        &:focus {
            box-shadow: none;
        }
    }

    &__form-password-input {
        height: 50px;
        margin-bottom: 24px;
        border: 2px solid rgba(44, 45, 61, 0.1);

        &:focus {
            box-shadow: none;
        }
    }

    &__form-check {
        &:focus {
            box-shadow: none;
        }
    }

    &__form-btn {
        @include nawabBtnOne;
        width: 100%;
        height: 43px;
        margin-bottom: 25px;

        &:hover {
            box-shadow: 0px, 3px rgba(31, 141, 237, 0.3);
        }
    }

    &__forgot-password {
        display: flex;
        justify-content: space-between;
    }

    &__forgot-btn {
        text-decoration: none;
        color: #EE2B47;
        font-size: 16px;
        font-weight: 400;
        margin-bottom: 50px;

        &:hover {
            color: red;
        }
    }

    &__text-area {
        display: flex;
        justify-content: space-between;
    }

    &__text {
        display: block;
        font-size: 16px;
        font-weight: 600;
        text-align: center;
        color: $primamryColorBlue;
        margin-bottom: 10px;
        margin-right: 10px;
        margin-left: 10px;
        width: 115px;

        @media only screen and (max-width: 600px) {
            width: 100%;
        }
    }

    &__left-line {
        border-top: 1px solid rgba(44, 45, 61, 0.1);
        width: 200px;
        margin-top: 10px;

        @media only screen and (max-width: 600px) {
            display: none;
        }
    }

    &__right-line {
        border-top: 1px solid rgba(44, 45, 61, 0.1);
        width: 200px;
        margin-top: 10px;

        @media only screen and (max-width: 600px) {
            display: none;
        }
    }

    &__social-icon {
        margin-top: 20px;
        text-align: center;
        margin-bottom: 40px;

        a {
            color: white;
            border-radius: 4px;
            margin: 0 5px 0 5px;

            &:hover {
                box-shadow: 0px, 3px rgba(31, 141, 237, 0.3);
                transition: ease-in-out 0.3s;
            }
        }
    }

    &__facebook-icon {
        background: #3B5998;
        padding: 7px 12px;
    }

    &__twitter-icon {
        background: #55ACEE;
        padding: 7px 9px;
    }

    &__linkedin-icon {
        background: #2867B2;
        padding: 7px 10px;
    }

    &__register-content {
        text-align: center;

        @media only screen and (max-width: 992px) {
            margin-bottom: 35px;
        }
    }

    &__register-btn {
        text-decoration: none;
        color: #EE2B47;
        font-size: 16px;
        font-weight: 400;

        &:hover {
            color: red;
        }
    }
}