.site-footer {
    background: $primamryColorBlue;

    &__footer-logo {
        margin-top: 10px;
        margin-bottom: 20px;
    }

    &__description {
        font-size: 16px;
        font-weight: 400;
        color: $primamryColorWhite;
        text-align: left;
        margin-bottom: 40px;
        max-width: 260px;
    }

    &__top {
        padding: 110px 0 65px 0;

        @media only screen and (max-width: 600px) {
            padding: 50px 0 30px 0;
        }
    }

    &__logo {
        margin-bottom: 20px;
    }

    &__btn {
        @include nawabBtnOne;
        padding: 11px 21px;
    }

    &__header {
        color: $primamryColorWhite;
        font-size: 18px;
        font-weight: 600;
        margin-bottom: 10px;
    }

    &__location {
        display: flex;
        margin-bottom: 10px;

        i {
            margin-top: 5px;
            margin-right: 10px;
        }
    }

    &__location-icon {
        margin-right: 8px;
        margin-top: -20px;

        @media only screen and (max-width: 600px) {
            margin-top: 5px;
        }
    }

    &__mail {
        display: flex;
        margin-bottom: 10px;

        i {
            margin-top: 8px;
            margin-right: 10px;
        }
    }

    &__mail-icon {
        margin-right: 8px;
        margin-top: 5px;
    }

    &__cell {
        display: flex;
        margin-bottom: 10px;

        i {
            margin-top: 5px;
            margin-right: 10px;
        }
    }

    &__cell-icon {
        margin-right: 8px;
    }

    &__quick-links,
    &__services-links {
        list-style: none;
        padding: 0;
        margin-top: 10px;

        li {
            margin-bottom: 10px;

            a {
                text-decoration: none;
                color: $primamryColorWhite;
                font-size: 16px;
                font-weight: 400;
            }
        }
    }

    &__links,
    &__services {
        @media only screen and (max-width: 600px) {
            width: 50%;
        }
    }

    &__address {
        margin-top: 10px;
        font-size: 16px;
        font-weight: 400;
        color: $primamryColorWhite;
        text-align: left;
    }

    &__bottom {
        border-top: 1px solid rgba(255, 255, 255, 0.1);
    }

    &__content {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        padding: 35px 0 35px 0;

        @media only screen and (max-width: 600px) {
            padding: 15px 0 20px 0;
        }
    }

    &__copyright {
        font-size: 14px;
        font-weight: 400;
        color: $primamryColorWhite;
        margin: 0;

        @media only screen and (max-width: 600px) {
            display: block;
            margin-left: auto;
            margin-right: auto;
        }
    }

    &__footer-links {
        display: flex;
        list-style: none;
        margin: 0;
        padding: 0;

        li {
            a {
                color: #ffffff;
                font-size: 14px;
                font-weight: 400;
                margin-left: 10px;
                text-decoration: none;

                @media only screen and (max-width: 600px) {
                    margin-right: 10px;
                    margin-left: 0;
                }
            }
        }
    }

    &__links-container {
        @media only screen and (max-width: 600px) {
            display: block;
            margin-left: auto;
            margin-right: auto;
        }
    }

    &__social-icon {
        margin-top: 20px;

        a {
            border: 2px Solid rgba(255, 255, 255, 0.1);
            color: white;
            border-radius: 4px;
            margin-right: 5px;

            &:hover {
                background: linear-gradient(184.96deg, #489cff 1.19%, #0075ff 101.54%);
            }
        }
    }

    &__facebook-icon {
        padding: 7px 12px;
    }

    &__twitter-icon {
        padding: 7px 9px;
    }

    &__vimeo-icon {
        padding: 7px 10px;
    }

    &__social-links {
        @media only screen and (max-width: 600px) {
            margin-top: 20px;
            text-align: center;
        }
    }
}