.support {
    background: #F8FAFE;
    padding: 110px 0 115px 0;

    @media only screen and (max-width: 600px) {
        padding: 40px 0 50px 0;
    }

    &__section-title {
        @include nawabSectionTitle;
        margin-bottom: 50px;

        @media only screen and (max-width: 600px) {
            margin-bottom: 20px;
        }
    }

    &__section-description {
        @include nawabSectionDescription;
        margin-bottom: 40px;

        @media only screen and (max-width: 600px) {
            margin-bottom: 30px;
        }
    }

    &__box {
        text-align: center;
        border: none;
        background: #ffffff;
        box-shadow: 0px 1px 3px rgba(44, 45, 61, 0.16);
        border-radius: 10px;
        padding: 40px 60px 30px 60px;
        margin-bottom: 30px;

        @media only screen and (max-width: 600px) {
            padding: 35px 25px;
            margin-bottom: -10px;
        }
    }

    &__box-title {
        font-size: 24px;
        font-weight: 500;
        color: #2c2d3d;
        margin-top: 15px;
        margin-bottom: 18px
    }

    &__box-description {
        @include serviceDescription;
    }

    &__form {
        padding: 50px 50px 47px 50px;
        background: $primamryColorWhite;
        border-radius: 10px;

        @media only screen and (max-width: 600px) {
            padding: 0 0 20px 0;
            margin-top: 35px;
        }

        input {
            height: 50px;

            &:focus {
                box-shadow: none;
            }
        }
    }

    &__form-question {
        margin-bottom: 40px;

        textarea {
            &:focus {
                box-shadow: none;
            }
        }
    }

    &__form-btn {
        @include nawabBtnOne;
        width: 137px;
        height: 43px;

        &:hover {
            box-shadow: 0px, 3px rgba(31, 141, 237, 0.3);
        }

        @media only screen and (max-width: 600px) {
            display: block;
            margin-left: auto;
            margin-right: auto;
        }
    }

    &__maps-body {
        width: 100%;
        height: 513px;
    }

    &__maps-body iframe {
        width: 100%;
        height: 100%;
        border-radius: 10px;
    }
}