.shoping-cart {
    padding: 10.63rem 0;

    &__top-part {
        padding: 1.75rem;
        background-color: #0075FF;
        border-radius: 10px 10px 0px 0px;
    }

    &__title {
        font-family: $paragraphFont;
        font-weight: 700;
        text-align: center;
        color: #fff;
        margin-bottom: 0.31rem;
        font-size: 1.5rem;
    }

    &__dec {
        font-family: $paragraphFont;
        font-weight: 400;
        text-align: center;
        color: #fff;
    }

    &__bottom-part {
        padding: 67px 50px;
        border-radius: 0 0 10px 10px;

        @media (max-width: 47.9375rem) {
            padding: 37px 10px;
        }
    }

    &__table-title {
        background-color: #F8FAFF;
        padding: 1.625rem 0.63rem 1.5rem;

    }

    &__domain-name {
        font-weight: 600;
        font-family: $paragraphFont;
    }

    &__order-summary-box {
        box-shadow: 0px 1px 3px rgba(4, 18, 38, 0.1);
        border-radius: 4px;
    }

    &__order-title {
        padding: 1.625rem 0.63rem 1.625rem;
        font-size: 1.125rem;
        background-color: #F8FAFF;
        font-weight: 700;
    }

    &__subtotal-title,
    &__subtotal-price {
        font-weight: 600;
        color: #2C2D3D;
        font-family: $paragraphFont;
    }

    &__cart-subtotal,
    &__total,
    &__order-price-total {
        padding: 1.35rem 0.63rem 1.625rem;
    }

    &__total {
        background-color: #F8FAFF;
    }

    &__select-drop-down {
        padding: 0;

        .select-item {
            border: 1px solid rgba(44, 45, 61, 0.1);
            border-radius: 2px;
            padding: 6px;
            font-size: 12px;
            color: #80818B;
        }
    }

    &__total-title,
    &__order-price-title {
        color: #80818B;
        font-size: 0.875rem;
    }

    &__order-price-title {
        font-size: 1.20rem;
        font-weight: 600;
        font-family: $paragraphFont;

        @media (max-width: 74.9375rem) {
            font-size: 1.11rem;
        }
    }

    &__order-price {
        font-size: 1.375rem;
        font-weight: 600;
        color: #2C2D3D;

        @media (max-width: 74.9375rem) {
            font-size: 1.20rem;
        }
    }

    &__checkout-btn,
    &__continue-shoping-btn {
        display: block;
        padding: 16px 24px;
        text-align: center;
        background: linear-gradient(184.96deg, #489CFF 1.19%, #0075FF 101.54%);
        box-shadow: 0px 3px 6px rgba(31, 141, 237, 0.3);
        border-radius: 4px;
        color: #fff;
        margin-top: 1.5rem;
        font-weight: 600;
        font-family: $paragraphFont;

        @media (max-width:37.5rem) {
            padding: 11px 24px;
        }
    }

    &__continue-shoping-btn {
        background: linear-gradient(184.96deg, #EE2B47 1.19%, #EE2B47 101.54%);
        border-radius: 4px;
        color: #fff;

    }

    &__cupon-area {
        @media (max-width: 61.9375rem) {
            margin-bottom: 2.5625rem;
        }
    }

    &__cupon-title {
        margin-top: 2.5625rem;
        margin-bottom: 1.88rem;
        font-size: 1.13rem;
        font-family: $paragraphFont;
        color: #2C2D3D;
        font-weight: 700;
    }

    &__cupon-box-input {
        max-width: 172px;
        margin-right: 10px;

        &:focus-visible {
            box-shadow: none;
            border-color: #ced4da;
        }
    }

    &__cupon-btn {
        padding: 7px 28px;
        display: inline-block;
        background-color: #0075FF;
        color: #fff;
        border-radius: 4px;
        font-family: $paragraphFont;
    }
}

tr {
    border: none;
}

table {
    margin: 0;
}

.table td,
.table th {
    padding: 0;
    padding: 1.122rem 0.63rem 1.625rem;
}

select:focus-visible {
    outline: none;
}

table {
    box-shadow: 0px 1px 3px rgba(4, 18, 38, 0.1);
    border-radius: 4px;
}