// button
@mixin nawabBtnOne {
    border: none;
    color: $primamryColorWhite;
    border-radius: 4px;
    transition: ease-in-out 0.3s;
    font-weight: 600;
    font-size: 16px;
    outline: none;
    background: linear-gradient(184.96deg, #489cff 1.19%, #0075ff 101.54%);
    position: relative;
    z-index: 1;
    text-decoration: none;

    &:hover {
        color: white;
        cursor: pointer;
        box-shadow: 0px 3px 20px rgba(31, 141, 237, 0.3);
    }
}

@mixin nawabBtnTwo {
    border: none;
    color: $primamryColorWhite;
    border-radius: 4px;
    transition: ease-in-out 0.3s;
    font-weight: 600;
    font-size: 16px;
    outline: none;
    border: 2px solid $primamryColorWhite;
    position: relative;
    z-index: 1;
    text-decoration: none;

    &:hover {
        color: black;
        cursor: pointer;
        border: 2px solid white;
    }

    &:before {
        transition: 0.5s all ease;
        position: absolute;
        top: 0;
        left: 50%;
        right: 50%;
        bottom: 0;
        opacity: 0;
        content: "";
        background-color: $primamryColorWhite;
    }

    &:hover::before {
        transition: 0.5s all ease;
        left: 0;
        right: 0;
        opacity: 1;
        z-index: -1;
    }
}

@mixin nawabBtnThree {
    border: none;
    color: #0075FF;
    border-radius: 4px;
    transition: ease-in-out 0.3s;
    font-weight: 600;
    font-size: 16px;
    outline: none;
    border: 2px Solid rgba(0, 117, 255, 0.1);
    position: relative;
    z-index: 1;
    text-decoration: none;

    &:hover {
        color: $primamryColorWhite;
        cursor: pointer;
        border: none;
        padding: 9px 23px;
    }

    &:before {
        transition: 0.5s all ease;
        position: absolute;
        top: 0;
        left: 50%;
        right: 50%;
        bottom: 0;
        opacity: 0;
        content: "";
        background: linear-gradient(184.96deg, #489cff 1.19%, #0075ff 101.54%);
        border-radius: 4px;
    }

    &:hover::before {
        transition: 0.5s all ease;
        left: 0;
        right: 0;
        opacity: 1;
        z-index: -1;
    }
}