.client-logo {
    &__logos {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        padding-bottom: 120px;

        @media only screen and (max-width: 600px) {
            padding-bottom: 40px;
        }
    }

    img {
        width: 100%;
        height: 40px;
    }
}