.server {
    padding: 100px 0px 120px 0px;

    @media only screen and (max-width: 996px) {
        overflow: hidden;
        overflow-x: auto;
        padding-top: 50px;
        padding-bottom: 50px;
    }

    &__table {
        background: $primamryColorBlue;
        color: #fff;
    }

    &__full-table{
        min-width: 950px;
    }

    &__order-now-btn {
        padding: 4px 15px;
        background: linear-gradient(184.96deg, #489CFF 1.19%, #0075FF 101.54%);
        box-shadow: 0px 1px 3px rgba(31, 141, 237, 0.3);
        border-radius: 4px;
        color: #fff;
        font-size: 1rem;
        font-family: $paragraphFont;
        font-weight: 600;
        display: inline-block;
    }
}

th {
    text-align: center;
}

th:first-child {
    border-radius: 10px 0px 0px 0px;
}

th:last-child {
    border-radius: 0px 10px 0px 0px;
}

td {
    text-align: center;
}

td:last-child {
    border-radius: 10px;
}

tr {
    border: 1px solid rgba(44, 45, 61, 0.1);
    ;
}