.hero-2 {
    &__image {
        background: url("/media/hero/hero-img-2.png");
        background-position: top center;
        background-repeat: no-repeat;
        background-size: cover;
        margin: 0;
        padding: 0;
        width: 100%;

        @media only screen and (max-width: 600px) {
            height: 500px;
        }
    }

    &__content {
        color: #ffffff;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        min-height: 700px;
        padding-top: 55px;

        @media only screen and (max-width: 600px) {
            padding-top: 100px;
            min-height: 500px;
        }
    }

    &__heading {
        @include heroTitle;
    }

    &__paragraph {
        @include heroDescription;
        padding: 24px 10px 10px 0px;

        @media only screen and (max-width: 600px) {
            padding: 35px 10px 50px 10px;
        }
    }
}