.swiper-slide {
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
}

.nawab-testimonial {
    margin-bottom: 120px;
    margin-top: 160px;

    @media only screen and (max-width: 600px) {
        margin-top: 50px;
        margin-bottom: 50px;
    }

    &__section-title {
        @include nawabSectionTitle;
        margin-bottom: 25px;
    }

    &__box {
        background: #ffffff;
        box-shadow: 0px 20px 20px rgba(44, 45, 61, 0.16);
        border-radius: 10px;
        margin-bottom: 75px;
        padding: 40px 160px;
        text-align: center;

        @media only screen and (max-width: 600px) {
            box-shadow: 0px 10px 20px rgba(44, 45, 61, 0.16);
            margin-bottom: 50px;
            padding: 20px 20px;
        }

        @media only screen and (max-width: 768px) {
            padding: 20px 20px;
        }
    }

    &__star-icon {
        margin-bottom: 30px;
    }

    &__content {
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 20px;
        @include nawabSectionDescription;
        font-size: 18px;
        line-height: 32px;
    }

    &__avater {
        margin-bottom: 12px;
        border-radius: 50%;
    }

    &__client-name {
        font-weight: 600;
        font-size: 18px;
        line-height: 26px;
        font-style: normal;
        color: $primamryColorBlue;
        margin: 0;
    }

    &__client-designation {
        font-weight: 400;
        font-size: 14px;
        line-height: 26px;
        font-style: normal;
        color: $primamryColorBlue;
        margin: 0;
    }
}

.swiper-pagination {
    position: relative;
}