// hero
@mixin heroTitle {
    font-size: 68px;
    font-weight: 800;
    letter-spacing: 2px;
    line-height: 82px;
    margin: 0;

    @media only screen and (max-width: 600px) {
        font-size: 45px;
        letter-spacing: 1px;
        line-height: 53px;
        margin-top: 0;
        padding: 0 10px;
    }
}

@mixin heroDescription {
    font-size: 22px;
    font-weight: 400;
    line-height: 26px;
    margin: 0;

    @media only screen and (max-width: 600px) {
        font-size: 22px;
        padding: 0 10px;
    }
}

// Card
@mixin cardBody {
    padding: 0 10px 5px 10px;
    background: $primamryColorWhite;
    box-shadow: 0px 1px 3px rgba(44, 45, 61, 0.16);
    border-radius: 4px;
    margin: 5px 5px;
    text-align: center;
}

@mixin cardHead {
    font-size: 40px;
    font-weight: 800;
    margin: 0;
    padding: 0;
}

@mixin cardParagraph {
    font-weight: 400;
    font-size: 13px;
    color: $primamryColorParagraph;
    padding: 0;
    margin: 0;

    span {
        font-size: 10px;
    }
}

// Service 
@mixin serviceHeading {
    font-size: 22px;
    font-weight: 700;
    color: $primamryColorBlue;
    margin: 0;
}

@mixin serviceDescription {
    font-size: 16px;
    font-weight: 400;
    color: $primamryColorParagraph;
    margin: 0;
}

@mixin serviceLink {
    text-decoration: none;
    font-size: 16px;
    font-weight: 600;
    color: $primamryColorBlue;
    margin: 0;
}