.site-header {
    background: $primamryColorWhite;
    width: 100%;
    position: absolute;
    z-index: 999999;

    &__topbar {
        border-bottom: 1px solid rgba(44, 45, 61, 0.1);
    }

    // topbar
    .topbar-menu {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        font-size: 14px;
        font-weight: 400;
        color: $primamryColorParagraph;
        padding: 8px 0;

        @media only screen and (max-width: 920px) {
            display: none;
        }

        &__contact {
            display: flex;
            flex-wrap: wrap;
            list-style: none;
            margin: 0;
            padding: 0;
        }

        &__call {
            margin-right: 10px;
        }

        &__call-icon {
            margin-right: 5px;
            margin-bottom: 2px;
            width: 12px;
        }
        &__mail-icon {
            margin-right: 5px;
            margin-bottom: 2px;
            width: 16px;
        }

        &__login-form-button {
            display: flex;
            flex-wrap: wrap;
            list-style: none;
            margin: 0;
            padding: 0;
        }

        &__login,
        &__sign-up {
            text-decoration: none;
            color: $primamryColorParagraph;
        }

        &__devider {
            margin: 0;
            padding: 0 10px;
        }
    }

    // Mobile navbar
    .navbar {
        display: flex;
        justify-content: space-between;
        margin: 0;
        padding: 19px 0;

        &__close-icon,
        &__menu-icon {
            width: 2em;
            margin-top: 0.2em;
            cursor: pointer;
        }

        &__icon {
            @media only screen and (max-width: 920px) {
                display: none;
            }
        }

        &__close-icon {
            float: right;
            margin: 25px;
        }

        &__menu-container {
            position: fixed;
            right: 0;
            top: 0;
            background: $primamryColorWhite;
            width: 100%;
            z-index: 999;
            transform: translateY(-100%);
            transition: transform 0.5s ease-in-out;
            display: block;
        }

        &__unorder-list {
            list-style-type: none;
            padding: 0;
            margin-top: 65px;
        }

        &__link {
            text-align: center;
            color: black;
            padding: 0.75em 2em;
            display: block;
            text-decoration: none;

            svg{
                display: none;
            }

            &:hover {
                background: rgb(243, 242, 242);
            }
        }

        &__list {
            &:hover {
                div {
                    visibility: visible;
                }
            }
        }

        @media only screen and (max-width: 920px) {
            &__menu-container {
                overflow: scroll;
                scrollbar-width: none;
                box-shadow: 0 10px 30px rgb(0 0 0 / 10%);

                &::-webkit-scrollbar {
                    display: none;
                }
            }

            // sub-menu
            &__sub-menu{
                display: none;
            }
            &__polygon-icon {
                display: none;
            }

            &__sub-menu-content {
                background: rgb(248, 248, 248);
                text-align: center;
            }

            &__sub-menu-content-second {
                background: rgb(248, 248, 248);
                text-align: center;
            }

            &__sub-menu-unorder-list {
                list-style: none;
                padding: 0;
            }

            &__sub-menu-list {
                padding: 12px 0;

                &:hover {
                    background: rgb(243, 242, 242);
                    transition: 0.3s ease-in-out;
                }
            }

            &__sub-menu-link {
                text-decoration: none;
                color: black;
            }

            &__sub-menu-list-icon {
                display: none;
            }

            &__sub-menu-list-header {
                margin: 0;
                font-weight: 400;
                font-size: 18px;
                color: $primamryColorBlue
            }

            &__sub-menu-list-description {
                display: none;
            }

            &__offer-container {
                display: none;
            }
        }

        // Desktop navbar
        @media only screen and (min-width: 920px) {
            .navbar {
                &__logo{
                    width: 148px;
                }
                
                &__menu-icon {
                    display: none;
                }

                &__menu-container {
                    transform: translateX(0);
                    position: unset;
                    display: block;
                    width: auto;
                    height: auto;
                    background: none;
                }

                &__close-icon {
                    display: none;
                }

                &__unorder-list {
                    display: flex;
                    margin: 0;
                }

                &__link {
                    color: black;
                    padding: 5px 18px;
                    font-size: 15px;
                    font-weight: 600;
                    font-family: "Urbanist", sans-serif;

                    &:hover {
                        background: none;
                        color: #0075ff;
                    }
                }

                &__cart-icon {
                    margin-left: 20px;
                    width: 16px;
                }

                &__search-icon {
                    width: 16px;
                }

                // sub-menu
                &__sub-menu {
                    visibility: hidden;
                    position: absolute;
                    padding-top: 5px;
                }

                &__polygon-icon {
                    margin-left: 40px;
                    margin-bottom: -15px;
                }

                &__sub-menu-content {
                    box-shadow: 0 10px 30px rgb(0 0 0 / 10%);
                    background: $primamryColorWhite;
                    padding: 20px 25px 15px 25px;
                    border-radius: 4px;
                    display: flex;
                    align-items: center;
                    margin-left: -150px;
                }

                &__sub-menu-content-second {
                    box-shadow: 0 10px 30px rgb(0 0 0 / 10%);
                    background: $primamryColorWhite;
                    padding: 20px 25px 15px 25px;
                    border-radius: 4px;
                    display: flex;
                    align-items: center;
                    margin-left: -350px;
                }

                &__sub-menu-unorder-list {
                    list-style: none;
                    padding: 0;
                }

                &__sub-menu-list {
                    display: flex;
                    padding: 10px;
                    border-radius: 4px;

                    &:hover {
                        background: rgb(243, 242, 242);
                        transition: 0.3s ease-in-out;
                    }
                }

                &__sub-menu-link {
                    text-decoration: none;
                    color: black;
                }

                &__sub-menu-list-icon {
                    margin-right: 10px;
                }

                &__sub-menu-list-header {
                    margin: 0;
                    font-weight: 600;
                    font-size: 18px;
                    color: $primamryColorBlue
                }

                &__sub-menu-list-description {
                    margin: 0;
                    width: 225px;
                    font-weight: 400;
                    font-size: 14px;
                    color: $primamryColorParagraph;
                }

                // Offer
                &__offer-container {
                    background: #000000;
                    text-align: center;
                    padding: 25px 20px;
                    border-radius: 4px;
                    margin-left: 5px;
                }

                &__offer-amount {
                    color: #0062a8;
                    margin: 0;
                    font-weight: 600;
                    font-size: 22px;
                }

                &__offer-on {
                    color: $primamryColorWhite;
                    margin: 0;
                    font-weight: 400;
                    font-size: 13px;
                }

                &__offer-item {
                    color: $primamryColorWhite;
                    margin: 0;
                    font-weight: 400;
                    font-size: 13px;
                    min-width: 95px;
                }

                &__offer-previus-price {
                    color: $primamryColorWhite;
                    margin: 0;
                    font-weight: 400;
                    font-size: 12px;
                }

                &__offer-price {
                    color: $primamryColorWhite;
                    margin: 0;
                    font-weight: 700;
                    font-size: 22px;
                }
            }
        }
    }

    // calass add from js file
    .open-nav {
        transform: translateX(0);
    }
}

.bar-wrapper {
    box-shadow: 0px 1px 3px rgba(44, 45, 61, 0.16);
}

.bar-wrapper.sticky {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99999;
    width: 100%;
    background-color: white;
    box-shadow: 0 10px 30px rgb(0 0 0 / 5%);
    animation: stickySlideDown 0.65s cubic-bezier(0.23, 1, 0.32, 1) both;
    transition: .4s all ease-in-out;
}

@-webkit-keyframes stickySlideDown {
    from {
        -webkit-transform: translateY(-100%);
        transform: translateY(-100%);
    }

    to {
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }
}

@keyframes stickySlideDown {
    from {
        -webkit-transform: translateY(-100%);
        transform: translateY(-100%);
    }

    to {
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }
}