.pricing-3 {
    padding-top: 100px;
    padding-bottom: 150px;

    @media only screen and (max-width: 776px) {
        overflow: hidden;
        overflow-x: auto;
        padding-top: 50px;
        padding-bottom: 50px;
    }

    &__container {
        min-width: 776px;
        display: flex;
        justify-content: space-between;
    }

    &__package-heading {
        width: 100%;
    }

    &__left-column-feature {
        padding: 10px 0;
    }

    &__column-feature {
        padding: 10px 0;
    }

    &__package-1 {
        width: 100%;

        &:hover {
            border: 1px solid #0075FF;
            border-radius: 10px;
        }
    }

    &__package-2 {
        width: 100%;

        &:hover {
            border: 1px solid #0075FF;
            border-radius: 10px;
        }
    }

    &__package-3 {
        width: 100%;

        &:hover {
            border: 1px solid #0075FF;
            border-radius: 10px;
        }
    }

    &__left-header {
        text-align: center;
        margin: 0;
        border-top: 1px solid rgba(44, 45, 61, 0.1);
        border-left: 1px solid rgba(44, 45, 61, 0.1);
        border-right: 1px solid rgba(44, 45, 61, 0.1);
        border-radius: 10px 10px 0px 0px;
        visibility: hidden;

        padding: 15px 0 20px 0;
        font-size: 24px;
        font-weight: 600;
        color: #2C2D3D;
    }

    &__left-price {
        text-align: center;
        margin: 0;
        visibility: hidden;

        padding-bottom: 20px;
        font-size: 22px;
        font-weight: 700;
        color: #80818B;
    }

    &__left-column {
        list-style: none;
        padding: 0;
        text-align: center;
        border-left: 1px solid rgba(44, 45, 61, 0.1);
        border-bottom: 1px solid rgba(44, 45, 61, 0.1);
        border-top: 1px solid rgba(44, 45, 61, 0.1);
        width: 100%;
        border-radius: 10px 0px 0px 10px;
    }

    &__left-column-feature {
        font-size: 16px;
        font-weight: 700;
        color: #80818B;
    }

    &__column-feature {
        font-size: 16px;
        font-weight: 400;
        color: #80818B;
    }

    &__header-1 {
        text-align: center;
        margin: 0;
        border-top: 1px solid rgba(44, 45, 61, 0.1);
        border-left: 1px solid rgba(44, 45, 61, 0.1);
        border-right: 1px solid rgba(44, 45, 61, 0.1);
        border-radius: 10px 10px 0px 0px;

        padding: 15px 0 20px 0;
        font-size: 24px;
        font-weight: 600;
        color: #2C2D3D;
    }

    &__price-1 {
        text-align: center;
        margin: 0;
        border-left: 1px solid rgba(44, 45, 61, 0.1);
        border-right: 1px solid rgba(44, 45, 61, 0.1);

        padding-bottom: 20px;
        font-size: 22px;
        font-weight: 700;
        color: #80818B;
    }

    &__column-1 {
        list-style: none;
        padding: 0;
        text-align: center;
        border-left: 1px solid rgba(44, 45, 61, 0.1);
        border-top: 1px solid rgba(44, 45, 61, 0.1);
        border-right: 1px solid rgba(44, 45, 61, 0.1);
        width: 100%;
    }

    &__header-2 {
        text-align: center;
        margin: 0;
        border-top: 1px solid rgba(44, 45, 61, 0.1);
        border-radius: 10px 10px 0px 0px;

        padding: 15px 0 20px 0;
        font-size: 24px;
        font-weight: 600;
        color: #2C2D3D;
    }

    &__price-2 {
        text-align: center;
        margin: 0;

        padding-bottom: 20px;
        font-size: 22px;
        font-weight: 700;
        color: #80818B;
    }

    &__column-2 {
        list-style: none;
        padding: 0;
        text-align: center;
        border-top: 1px solid rgba(44, 45, 61, 0.1);
        width: 100%;
    }

    &__header-3 {
        text-align: center;
        margin: 0;
        border-top: 1px solid rgba(44, 45, 61, 0.1);
        border-left: 1px solid rgba(44, 45, 61, 0.1);
        border-right: 1px solid rgba(44, 45, 61, 0.1);
        border-radius: 10px 10px 0px 0px;

        padding: 15px 0 20px 0;
        font-size: 24px;
        font-weight: 600;
        color: #2C2D3D;
    }

    &__price-3 {
        text-align: center;
        margin: 0;
        border-left: 1px solid rgba(44, 45, 61, 0.1);
        border-right: 1px solid rgba(44, 45, 61, 0.1);

