        text-align: center;
        border-left: 1px solid rgba(44, 45, 61, 0.1);
        border-top: 1px solid rgba(44, 45, 61, 0.1);
        border-right: 1px solid rgba(44, 45, 61, 0.1);
        width: 100%;
    }

    &__button-1 {
        text-align: center;
        border-bottom: 1px solid rgba(44, 45, 61, 0.1);
        visibility: hidden;
        padding: 55px 0;
    }

    &__button-2 {
        text-align: center;
        border-bottom: 1px solid rgba(44, 45, 61, 0.1);
        border-left: 1px solid rgba(44, 45, 61, 0.1);
        border-right: 1px solid rgba(44, 45, 61, 0.1);
        padding: 55px 0;
        border-radius: 0px 0px 10px 10px;
    }

    &__button-3 {
        text-align: center;
        border-bottom: 1px solid rgba(44, 45, 61, 0.1);
        padding: 55px 0;
        border-radius: 0px 0px 10px 10px;
    }

    &__button-4 {
        text-align: center;
        border-bottom: 1px solid rgba(44, 45, 61, 0.1);
        border-left: 1px solid rgba(44, 45, 61, 0.1);
        border-right: 1px solid rgba(44, 45, 61, 0.1);
        border-radius: 0px 0px 10px 10px;
        padding: 55px 0;
    }

    &__box-btn {
        @include nawabBtnThree;
        padding: 9px 23px;
    }

    li:nth-child(even) {
        background-color: #FAFAFA;
    }

    ul {
        margin: 0;
    }
}