.nawab-pricing-2 {
    padding: 120px 0 90px 0;

    @media only screen and (max-width: 600px) {
        padding: 50px 0 50px 0;
    }

    &__box {
        text-align: center;
        border: none;
        background: #ffffff;
        box-shadow: 0px 1px 3px rgba(44, 45, 61, 0.16);
        border-radius: 10px;
        padding: 50px 50px 60px 50px;
        margin-bottom: 20px;

        &:hover {
            background: #ffffff;
            box-shadow: 0px 20px 20px rgba(44, 45, 61, 0.16);
            transition: 0.5s;
        }
    }

    &__box-title {
        @include serviceHeading;
        margin-top: 15px;
        font-size: 24px;
    }

    &__box-price-amount {
        color: $primamryColorParagraph;
        font-family: urban;
        font-weight: 700;
        font-size: 18px;
        font-style: normal;
    }

    &__box-price-duration {
        color: $primamryColorParagraph;
        font-weight: 700;
        font-size: 18px;
        font-style: normal;
    }

    &__box-feature {
        @include serviceDescription;
        list-style: none;
        margin-bottom: 50px;
        text-align: left;
        padding: 0;

        li {
            margin-bottom: 15px;
        }
    }

    &__storage {
        color: #2C2D3D;
        font-weight: 700;
    }

    &__box-btn {
        @include nawabBtnThree;
        padding: 9px 23px;
    }

    &__box-price {
        padding: 0 0 25px 0;
    }

    &__tick-icon {
        margin-right: 10px;
    }
}