.about {
    padding: 120px 0 120px 0;

    @media only screen and (max-width: 600px) {
        padding: 50px 0 50px 0;
    }

    &__group-title {
        @include nawabSectionTitle;
        text-align: left;

        @media only screen and (max-width: 992px) {
            text-align: center;
        }
    }

    &__group-description {
        text-align: left;
        max-width: 610px;
        font-weight: 400;
        font-size: 16px;
        line-height: 26px;
        color: $primamryColorParagraph;

        @media only screen and (max-width: 992px) {
            text-align: center;
            max-width: 100%;
            margin: 10px 0 30px 0;
        }
    }

    &__group-left {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 100px;

        @media only screen and (max-width: 992px) {
            display: block;
            text-align: center;
        }
    }

    &__group-right {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-direction: row-reverse;

        @media only screen and (max-width: 992px) {
            display: block;
            text-align: center;
        }
    }
}