.call-to-action {
    position: relative;
    background: $primamryColorBlue;
    z-index: 1;
    min-height: 370px;

    &__overlay {
        position: absolute;
        background: url("/media/call-to-action/call-to-action-bg.png");
        background-position: top center;
        background-repeat: no-repeat;
        background-size: cover;
        margin: 0;
        padding: 0;
        min-height: 370px;
        width: 100%;
        z-index: -1;
        opacity: 0.3;
    }

    &__content {
        padding: 77px 0 77px 0;
    }

    &__title {
        @include nawabSectionTitle;
        color: $primamryColorWhite;
    }

    &__buttons {
        text-align: center;
        margin-top: 60px;
        z-index: 1;
    }

    &__chat-btn {
        @include nawabBtnOne;
        font-size: 16px;
        font-weight: 500;
        padding: 11px 20px;
        margin: 0 2px 0 2px;
    }

    &__call-btn {
        @include nawabBtnTwo;
        font-size: 16px;
        font-weight: 500;
        padding: 9px 27px;
        margin: 0 2px 0 2px;
    }

    &__chat-icon {
        margin-right: 5px;
        margin-bottom: 2px;
        font-size: 14px;
    }

    &__call-icon {
        margin-right: 5px;
        margin-bottom: 2px;
        font-size: 12px;

        &:hover {
            color: black;
        }
    }
}