.discover-domainName {
    padding: 120px 0px 100px;

    @media only screen and (max-width: 600px) {
        padding: 40px 0px 60px;
    }

    &__title {
        @include nawabSectionTitle;
        margin-bottom: 13px;

    }

    &__description {
        @include nawabSectionDescription;
        margin-bottom: 30px;
    }

    &__box {
        box-shadow: 0px 1px 3px rgba(44, 45, 61, 0.16);
        border-radius: 10px;
        padding: 15px 20px 16px 20px;

        .domain-img {
            width: 87px;
            height: 42px;
        }
    }

    &__dec {
        color: #80818B;
        max-width: 138px;
        margin: 15px 0px 17px;
    }

    &__cupon-btn {
        margin-bottom: 21px;
        padding: 4px 8px 4px 8px;
        background: rgba(238, 43, 71, 0.06);
        border-radius: 2px;
        display: inline-block;

        a {
            color: #EE2B47;
            font-weight: 400;
            font-size: 12px;
            font-family: $headingFont;
        }
    }

    &__offers {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;

        .discount-price {
            font-family: $paragraphFont;
            font-weight: 600;
            font-size: 18px;

            .discount-amount {
                font-family: $paragraphFont;
                font-size: 12px;
                color: #EE2B47;
                margin-left: 8px;
                position: relative;
                font-weight: 400;

                &::after {
                    content: '';
                    position: absolute;
                    width: 49px;
                    height: 1px;
                    background-color: #EE2B47;
                    left: 0;
                    top: 7px;
                }
            }
        }
    }

    &__getoffer-btn {
        font-weight: 600;
        color: #80818B;
        font-family: $paragraphFont;
        position: relative;

        i {
            margin-left: 5px;
            font-size: 14px;
        }
    }

    &__plan-content {
        margin-top: 36px;
    }

    &__plan-button {
        @include nawabBtnOne;
        padding: 11px 20px;
        margin: 0 2px 0 2px;
    }
}