.nawab-pricing {
    padding: 140px 0 100px 0;

    @media only screen and (max-width: 600px) {
        padding: 50px 0 50px 0;
    }

    &__section-title {
        @include nawabSectionTitle;
        margin-bottom: 20px;
    }

    &__section-description {
        @include nawabSectionDescription;
        margin-bottom: 35px;
    }

    &__box {
        text-align: center;
        border: none;
        background: #ffffff;
        box-shadow: 0px 1px 3px rgba(44, 45, 61, 0.16);
        border-radius: 10px;
        padding: 50px 26px;
        margin-bottom: 20px;

        &:hover {
            background: #ffffff;
            box-shadow: 0px 20px 20px rgba(44, 45, 61, 0.16);
            transition: 0.5s;
        }
    }

    &__box-title {
        @include serviceHeading;
        margin-top: 15px;
    }

    &__box-price-amount {
        color: $primamryColorRed;
        font-family: urban;
        font-weight: 700;
        font-size: 30px;
        font-style: normal;
        margin-bottom: 27px;
    }

    &__box-price-duration {
        color: $primamryColorBlue;
        font-weight: 400;
        font-size: 12px;
        line-height: 40px;
        font-style: normal;
    }

    &__box-feature {
        @include serviceDescription;
        list-style: none;
        margin-bottom: 40px;
        padding: 0 0 0 20%;
        text-align: left;

        li {
            margin-bottom: 15px;
        }
    }

    &__box-btn {
        @include nawabBtnThree;
        padding: 9px 23px;
    }

    &__box-price {
        padding: 20px 0 25px 0;
    }

    &__tick-icon {
        margin-right: 10px;
    }
}