.cart-two {
    &__wrapper {
        background: #130954;
        padding: 160px 0 50px 0;
    }

    &__title {
        font-size: 2rem;
        font-family: $paragraphFont;
        color: #fff;
        font-weight: 600;
        margin-bottom: 0.94rem;
    }

    &__dec {
        font-size: 1rem;
        font-family: $paragraphFont;
        color: #FFFFFF;
        opacity: 0.8;
        font-weight: 400;
    }

    &__review-box {
        padding: 60px 0;

        @media only screen and (max-width: 600px) {
            padding: 50px 10px;
        }
    }

    &__dropdown {
        background: #0075FF;
        color: #fff;
        padding: 15px 20px;
        border-radius: 3px;
        max-width: 270px;
        display: flex;
        justify-content: space-between;
        font-size: 1.1rem;
        cursor: pointer;
        box-shadow: 0 0 1px rgba(0, 0, 0, 0.3);
        font-family: $paragraphFont;
        font-weight: 600;
        margin-bottom: 10px;

        @media (max-width: 1199px) {
            max-width: 224px;
        }

        @media (max-width: 767px) {
            max-width: 270px;
        }
    }

    &__category {
        font-weight: 600;
        font-family: $paragraphFont;
    }

    &__dropdown-menu {
        display: none;
        flex-direction: column;
        margin-top: -8px;
        max-width: 270px;
        box-shadow: 0 0 5px -1px rgba(0, 0, 0, 0.3);
        background: #fafafa;
        transform-origin: top left;
        margin-bottom: 15px;

        @media (max-width: 1199px) {
            max-width: 224px;
        }

        @media(max-width: 767px) {
            max-width: 270px;
        }

        span {
            padding: 14px 0 14px 25px;
            flex-grow: 1;
            width: 100%;
            box-sizing: border-box;
            cursor: pointer;
            transition: background 0.3s ease;
            background: #1C84FF;

            &:not(:last-child) {
                border-bottom: 1px solid #ffffff47;
            }

            .hosting-pages {
                font-weight: 500;
                font-family: $paragraphFont;
                font-size: 15px;
                color: #fff;
                opacity: .8
            }
        }
    }

    &__dropdown-menu-two {
        @extend .cart-two__dropdown-menu;
    }

    &__review-title {
        font-weight: 700;
        font-size: 24px;
        color: #2C2D3D;
        margin-bottom: 10px;

        @media(max-width: 767px) {
            margin: 60px 0 30px;
        }
    }

    &__divider {
        border: 1px solid rgba(44, 45, 61, 0.1);
        margin-bottom: 20px;
    }

    &__product-box {
        background: #FFFFFF;
        box-shadow: 0px 1px 3px rgba(44, 45, 61, 0.2);
        border-radius: 4px 4px 0px 4px;

        .product-heading {
            display: flex;
            justify-content: space-between;
            align-items: center;
            background-color: #0075FF;
            padding: 12px 20px;
            border-radius: 4px 4px 0 0;

            .product-title {
                font-size: 1rem;
                font-weight: 600;
                color: #fff;
                opacity: .8;
                font-family: $paragraphFont;
            }
        }

        .product-item {
            display: flex;
            justify-content: space-between;

            &:not(:nth-child(3)) {
                padding: 24px 20px;
            }

            &:nth-child(3) {
                padding: 10px 20px;
                background: #F9F9F9;
            }

            .left-side {
                .domain-reg-title {
                    font-size: 15px;
                    font-weight: 500;
                    color: #80818B;
                }

                .domain-name-tag {
                    font-weight: 500;
                    font-size: 12px;
                    color: #0075FF;
                }
            }

            .right-side {
                display: flex;

                .price-cycle {
                    .item-price {
                        font-weight: 600;
                        font-size: 15px;
                        color: #80818B;
                        margin-bottom: 10px;
                    }

                    .item-select {
                        border: 1px solid rgba(44, 45, 61, 0.1);
                        border-radius: 2px;
                        font-weight: 600;
                        font-size: 12px;
                        color: #A2A2A2;
                        padding: 6px 4px;
                        margin-bottom: 10px;
                    }

                    .reneual-price {
                        font-weight: 400;
                        font-size: 13px;
                        color: #80818B;
                    }
                }
            }
        }

    }

    &__empty-cart {
        text-align: end;
        margin-bottom: 60px;

        .empty-cart-btn {
            display: inline-block;
            padding: 8px 10px;
            background: #F5F5F5;
            box-shadow: 0px 1px 3px rgba(44, 45, 61, 0.2);
            border-radius: 0px 0px 4px 4px;
            font-size: 10px;
            font-weight: 600;
            color: #80818B;
        }
    }

    &__promo-code {
        .promo-code-title {
            @extend .cart-two__review-title;
            margin-bottom: 30px;
            text-align: left !important;
        }

        .promo-code-box {
            padding: 26px 18px;
            box-shadow: 0px 1px 3px rgba(44, 45, 61, 0.2);

            @media (max-width: 991px) {
                margin-bottom: 30px;
            }

            .promo-code-input {
                margin-right: 25px;
                max-width: 347px;
                padding-left: 20px;
                border: 1px solid rgba(44, 45, 61, 0.1);
                border-radius: 4px;

                @media (max-width: 1200px) {
                    max-width: 241px;
                    font-size: 14px;
                }

                @media (max-width: 991px) {
                    max-width: 464px;

                }

                @media (max-width: 767px) {
                    max-width: 464px;
                    margin: auto;
                    margin-bottom: 30px;
                }
            }

            .promo-code-btn {
                display: inline-block;
                background: linear-gradient(184.96deg, #489CFF 1.19%, #0075FF 101.54%);
                box-shadow: 0px 3px 6px rgba(31, 141, 237, 0.3);
                border-radius: 4px;
                color: #fff;
                padding: 20px;
                display: flex;
                flex-direction: row;
                justify-content: center;
                align-items: center;
                padding: 11px 21px;
                gap: 6px;

                @media (max-width: 767px) {
                    max-width: 200px;
                    margin: auto;
                }
            }
        }
    }

    &__order-summary-box {
        background: #0075FF;
        box-shadow: 0px 1px 3px rgba(4, 18, 38, 0.1);
        border-radius: 4px;
        padding: 20px 20px 29px 20px;

        .top-part {
            .order-summary-title {
                font-size: 18px;
                font-weight: 700;
                color: #fff;
                margin-bottom: 34px;
            }

            .subtotal-box {
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin-bottom: 15px;

                .subtotal-tag,
                .subtotal-price {
                    font-size: 14px;
                    font-weight: 500;
                    color: #fff;
                }
            }

            .annualy-total {
                display: flex;
                justify-content: space-between;
                align-items: end;
                color: #fff;
                padding: 14px 0;
                border-top: 1px solid rgba(255, 255, 255, 0.5);
                border-bottom: 1px solid rgba(255, 255, 255, 0.5);
            }

            .due-total {
                display: flex;
                justify-content: space-between;
                align-items: center;
                color: #fff;
                margin: 31.5px 0 31px;

                @media(max-width: 1200px) {
                    align-items: end;
                }

                .due-tag {
                    font-size: 12px;
                    font-weight: 600;
                }

                .due-price {
                    font-weight: 600;
                    font-size: 1rem;
                }
            }

            .check-out {
                text-align: center;
                margin-bottom: 40px;

                .checkout-btn {
                    padding: 11px 21px;
                    box-shadow: 0px 3px 6px rgba(31, 141, 237, 0.3);
                    border-radius: 4px;
                    color: #0075FF;
                    background: #fff;
                    font-weight: 600;
                    font-size: 1rem;
                }
            }

            .continue-shoping {
                .continue-shoping-btn {
                    font-size: 14px;
                    font-weight: 600;
                    color: #fff;
                    opacity: .8;
                }
            }
        }
    }
}

.rotate-dropdown-arrow {
    transform: rotate(-180deg);
}


#openDropdown:checked+.cart-two__dropdown-menu {
    display: flex;
    animation: animate 0.4s ease 0s 1 forwards;
}

#openDropdown-two:checked+.cart-two__dropdown-menu {
    display: flex;
    animation: animate 0.4s ease 0s 1 forwards;
}

@keyframes animate {
    from {
        transform: rotateX(50deg);
    }

    to {
        transform: rotateX(0deg);
    }
}

.action-btn {
    @extend .cart-two__dropdown;
    align-items: center;

}

label {
    display: block;
}

.dropdown-menu-two {
    @extend.cart-two__dropdown-menu;
    margin-top: -8px;
}

.mr {
    margin-right: 6px;
}