.hero-domain {
    &__image {
        background: url("/media/hero/hero-img-2.png");
        background-position: top center;
        background-repeat: no-repeat;
        background-size: cover;
        margin: 0;
        padding: 0;
        width: 100%;
    }

    &__content {
        color: #ffffff;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        min-height: 700px;
        padding-top: 55px;
    }

    &__heading {
        @include heroTitle;
    }

    &__paragraph {
        @include heroDescription;
        padding: 24px 10px 0px 10px;

        @media only screen and (max-width: 600px) {
            padding: 15px 10px 0px 10px;
        }
    }

    // search bar
    &__search-area {
        padding: 0px 20px;
        margin-top: 50px;
    }

    &__wrap {
        max-width: 800px;
        background: $primamryColorWhite;
        box-shadow: 0px 1px 3px rgb(44 45 61 / 16%);
        padding: 3px;
        border-radius: 4px;
        margin: 20px 20px;

        @media only screen and (max-width: 600px) {
            width: 100%;
        }
    }

    &__search {
        width: 100%;
        display: flex;
    }

    &__searchTerm {
        width: 100%;
        border: none;
        outline: none;
        background: none;
        padding-left: 20px;
        font-weight: 500;
        color: $primamryColorParagraph;
        font-size: 16px;
    }

    &__searchButton {
        background: linear-gradient(184.96deg, #489cff 1.19%, #0075ff 101.54%);
        border: none;
        border-radius: 4px;
        text-align: center;
        color: #fff;
        cursor: pointer;
        font-size: 16px;
        padding: 9px 24px;
    }

    &__domain-select {
        width: 100px;
        border: none;
        margin-right: 50px;

        &:focus {
            box-shadow: none;
        }

        @media only screen and (max-width: 600px) {
            margin-right: 10px;
        }
    }

    &__domain-select-option {
        margin-top: 20px;

        &:focus-visible {
            outline: none;
        }
    }

    &__domain-name {
        display: flex;
        margin-top: 40px;
        justify-content: center;
        flex-wrap: wrap;
    }

    &__com {
        @include cardBody;

        h3 {
            @include cardHead;
            color: #4dbbea;

            span {
                color: #0062a8;
            }
        }

        p {
            @include cardParagraph;
        }
    }

    &__net {
        @include cardBody;

        h3 {
            @include cardHead;
            color: #fdcc0f;

            span {
                color: #231f20;
            }
        }

        p {
            @include cardParagraph;
        }
    }

    &__co {
        @include cardBody;

        h3 {
            @include cardHead;
            color: #f76b01;

            span {
                color: #f76b01;
            }
        }

        p {
            @include cardParagraph;
        }
    }

    &__org {
        @include cardBody;

        h3 {
            @include cardHead;
            color: #193465;

            span {
                color: #193465;
            }
        }

        p {
            @include cardParagraph;
        }
    }

    &__info {
        @include cardBody;

        h3 {
            @include cardHead;
            color: #09547f;

            span {
                color: #09547f;
            }
        }

        p {
            @include cardParagraph;
        }
    }

    &__me {
        @include cardBody;

        h3 {
            @include cardHead;
            color: #ed4239;

            span {
                color: #ed4239;
            }
        }

        p {
            @include cardParagraph;
        }
    }
}