// Section 
@mixin nawabSectionTitle {
    font-size: 46px;
    line-height: 56px;
    font-weight: 800;
    color: $primamryColorBlue;
    text-align: center;
    font-style: normal;

    @media only screen and (max-width: 600px) {
        font-size: 30px;
        line-height: 40px;
    }
}

@mixin nawabSectionDescription {
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    color: $primamryColorParagraph;
    text-align: center;
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
}