@import "./base/variables";
@import "./base/mixins";


@font-face {
	font-family: mulish;
	src: url(/assets/fonts/mulish/Mulish-Bold.ttf);
	src: url(/assets/fonts/mulish/Mulish-ExtraBold.ttf);
	src: url(/assets/fonts/mulish/Mulish-Medium.ttf);
	src: url(/assets/fonts/mulish/Mulish-Regular.ttf);
	src: url(/assets/fonts/mulish/Mulish-SemiBold.ttf);
}

@font-face {
	font-family: urban;
	src: url(/assets/fonts/urbanist/Urbanist-Bold.ttf);
	src: url(/assets/fonts/urbanist/Urbanist-ExtraBold.ttf);
	src: url(/assets/fonts/urbanist/Urbanist-Medium.ttf);
	src: url(/assets/fonts/urbanist/Urbanist-Regular.ttf);
	src: url(/assets/fonts/urbanist/Urbanist-SemiBold.ttf);
}


body {
	font-family: mulish;
}

html {
	font-size: 16px;
	scroll-behavior: smooth;
}


input,
textarea {
	width: 100%;
	outline: 0;
}

button {
	cursor: pointer;
	outline: 0;
}

a,
a:hover {
	text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: urban;
	margin: 0;
	padding: 0;
}

P {
	margin: 0;
	padding: 0;
}

li {
	list-style: none;
	padding: 0;
	margin: 0;
}

img {
	max-width: 100%;
	height: auto;
}

@media (min-width: 1200px) {

	.container,
	.container-lg,
	.container-md,
	.container-sm,
	.container-xl {
		max-width: 1170px;
	}
}