.chat {
    position: fixed;
    bottom: 20px;
    right: 0;
    z-index: 99999;

    &__container {
        max-width: 270px;
        width: 270px;
    }

    &__header-container {
        background: $primamryColorWhite;
        border-radius: 10px;
        margin-right: 10px;
        box-shadow: 0 1px 3px rgb(45, 45, 61, 0.16);
    }

    &__header {
        background: $primamryColorRed;
        border-radius: 10px 10px 0 0;
        padding: 0;
        margin: 0;
        display: flex;
        justify-content: space-between;
    }

    &__close-button {
        color: white;
        padding: 13px 20px 0px 20px;
        cursor: pointer;
    }

    &__header-text {
        color: $primamryColorWhite;
        font-size: 16px;
        font-weight: 500;
        padding: 10px 0 10px 20px;
        margin: 0;
        text-align: left;
    }

    &__input {
        background: none;
        border: none;
        padding: 10px 20px;
        border-top: 1px Solid rgba(44, 45, 61, 0.1);
        width: 100%;
        font-size: 12px;

        &:focus-visible {
            outline: none;
        }
    }

    &__attach-option {
        display: flex;
        justify-content: space-between;
        padding: 0 20px 10px 20px;
    }

    &__msg-box {
        max-height: 250px;
        height: 250px;
        overflow-y: scroll;
        padding: 5px 0;
    }

    &__chat-button-link {
        text-decoration: none;
        cursor: pointer;
    }

    &__chat-button {
        display: flex;
        justify-content: end;
    }

    &__button-element {
        margin-top: -65px;
        margin-left: 25px;
    }

    &__chat-msg-icon {
        margin-left: 5px;
    }

    &__button-text {
        color: $primamryColorWhite;
        font-weight: 600;
        font-size: 14px;
    }

    &__gallery{
        cursor: pointer;
    }

    &__send{
        cursor: pointer;
    }

    &__user-msg-container {
        display: flex;
        justify-content: flex-start;
    }

    &__user-msg {
        background: $primamryColorWhite;
        color: $primamryColorParagraph;
        display: inline-block;
        font-size: 12px;
        border-radius: 4px;
        margin: 5px 45px 5px 20px;
        padding: 5px 10px;
        box-shadow: 0 1px 3px rgb(45, 45, 61, 0.16);
    }

    &__admin-msg-container {
        display: flex;
        justify-content: flex-end;
    }

    &__admin-msg {
        background: $primamryColorRed;
        color: $primamryColorWhite;
        display: inline-block;
        font-size: 12px;
        border-radius: 4px;
        margin: 5px 20px 5px 45px;
        padding: 5px 10px;
        box-shadow: 0 1px 3px rgb(45, 45, 61, 0.16);
    }
}

.open-chatbox {
    display: none;
}