.nawab-funfact {
    background: $primamryColorBlue;
    color: $primamryColorWhite;
    text-align: center;
    position: relative;
    z-index: 1;
    min-height: 270px;

    @media only screen and (max-width: 600px) {
        min-height: 170px;
    }

    &__overlay {
        position: absolute;
        background: url("/media/funfact/funfact-bg.png");
        background-position: top center;
        background-repeat: no-repeat;
        background-size: cover;
        margin: 0;
        padding: 0;
        min-height: 270px;
        width: 100%;
        z-index: -1;
    }

    &__content {
        padding: 75px 0 70px 0;

        @media only screen and (max-width: 600px) {
            padding: 45px 0 40px 0;
        }
    }

    &__counter {
        font-size: 56px;
        font-weight: 900;

        @media only screen and (max-width: 600px) {
            font-size: 35px;
        }
    }

    &__title {
        font-size: 30px;
        font-weight: 600;

        @media only screen and (max-width: 600px) {
            font-size: 20px;
        }
    }
}