.nawab-exclusive-service {
    padding: 110px 0 100px 0;

    @media only screen and (max-width: 600px) {
        padding: 80px 0 60px 0;
    }

    &__section-title {
        @include nawabSectionTitle;
        margin-bottom: 11px;

        @media only screen and (max-width: 600px) {
            margin-bottom: 7px;
        }
    }

    &__section-description {
        @include nawabSectionDescription;
        margin-bottom: 40px;

        @media only screen and (max-width: 600px) {
            margin-bottom: 30px;
        }
    }

    &__box {
        text-align: center;
        border: none;
        background: #ffffff;
        box-shadow: 0px 1px 3px rgba(44, 45, 61, 0.16);
        border-radius: 10px;
        padding: 50px 26px;
        margin-bottom: 20px;

        &:hover {
            background: #ffffff;
            box-shadow: 0px 20px 20px rgba(44, 45, 61, 0.16);
            transition: 0.5s;
        }

        @media only screen and (max-width: 600px) {
            padding: 35px 25px;
        }
    }

    &__box-title {
        @include serviceHeading;
        margin-top: 32px;
    }

    &__box-description {
        @include serviceDescription;
        margin: 19px 0 28px 0;
    }

    &__box-link {
        @include serviceLink;
        color: #80818B;

        &:hover {
            transition: 0.2s ease-in-out;
            color: #0075FF;

            path {
                transition: 0.2s ease-in-out;
                stroke: #0075FF;
            }
        }
    }

    &__arrow-icon {
        font-size: 13px;
    }
}