.service {
    background: #f8fafe;
    padding: 115px 0 120px 0;

    @media only screen and (max-width: 600px) {
        padding: 50px 0 50px 0;
    }

    &__section-title {
        @include nawabSectionTitle;
        margin-bottom: 13px;
    }

    &__section-description {
        @include nawabSectionDescription;
        margin-bottom: 30px;
    }

    &__box {
        border: none;
        background: #ffffff;
        box-shadow: 0px 1px 3px rgba(44, 45, 61, 0.16);
        border-radius: 10px;
        padding: 40px 70px 40px 40px;
        display: flex;

        &:hover {
            background: #ffffff;
            box-shadow: 0px 20px 20px rgba(44, 45, 61, 0.16);
            transition: 0.5s;
            cursor: pointer;
        }

        @media only screen and (max-width: 600px) {
            display: block;
            text-align: center;
            padding: 25px 35px 30px 35px;
        }
    }

    &__icon {
        width: 60px;
        height: 60px;
        margin-right: 30px;
    }

    &__card-title {
        font-size: 22px;
        font-weight: 600;
        color: $primamryColorBlue;
        margin-bottom: 10px;

        &:hover {
            transition: 0.2s ease-in-out;
            color: $primamryColorRed;
        }

        @media only screen and (max-width: 600px) {
            text-align: center;
            margin: 20px 0 10px 0;
        }
    }

    &__card-text {
        @include nawabSectionDescription;
        text-align: left;
        margin: 0;

        @media only screen and (max-width: 600px) {
            text-align: center;
        }
    }
}