.domain-name {
    padding: 175px 0 0 0;

    // search bar
    &__search-area {
        padding: 0px 20px;
    }

    &__wrap {
        max-width: 800px;
        background: $primamryColorWhite;
        box-shadow: 0px 1px 3px rgb(44 45 61 / 16%);
        padding: 3px;
        border-radius: 4px;
        margin: 0px 20px 60px 20px;

        @media only screen and (max-width: 600px) {
            width: 100%;
        }
    }

    &__search {
        width: 100%;
        display: flex;
    }

    &__searchTerm {
        width: 100%;
        border: none;
        outline: none;
        background: none;
        padding-left: 20px;
        font-weight: 500;
        color: $primamryColorParagraph;
        font-size: 16px;
    }

    &__searchButton {
        background: linear-gradient(184.96deg, #489cff 1.19%, #0075ff 101.54%);
        border: none;
        border-radius: 4px;
        text-align: center;
        color: #fff;
        cursor: pointer;
        font-size: 16px;
        padding: 9px 24px;
    }

    &__domain-select {
        width: 100px;
        border: none;
        margin-right: 50px;

        &:focus {
            box-shadow: none;
        }

        @media only screen and (max-width: 600px) {
            margin-right: 10px;
        }
    }

    &__domain-select-option {
        margin-top: 20px;

        &:focus-visible {
            outline: none;
        }
    }

    &__result {
        box-shadow: 0px 20px 20px rgba(44, 45, 61, 0.16);
    }

    &__timing {
        text-align: end;
        font-family: $headingFont;
        font-size: .88rem;
        color: #80818B;

        @media(max-width: 288px) {
            font-size: .66rem;
        }
    }

    &__search-result {
        background: #2C2D3D;
        border-radius: 10px 10px 0px 0px;
        padding: 8px 30px;
        display: flex;
        justify-content: space-between;
        align-items: center;

        @media (max-width: 420px) {
            display: block;
        }
    }

    &__dec {
        color: #fff;
        font-family: $headingFont;
        font-size: 1.38rem;
        font-weight: 500;

        @media (max-width: 420px) {
            margin-bottom: .69rem;
        }

        @media (max-width: 496px) {
            font-size: 1rem;
        }
    }

    &__right {
        width: 1rem;
        height: 1rem;
        background-color: #fff;
        border-radius: 10px;
    }

    &__base-box {
        border-radius: 50%;
        padding: 20px 30px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__base-price-right {
        &__heading {
            font-weight: 600;
            font-size: 2rem;
            color: #EE2B47;

            @media (max-width: 310px) {
                font-size: 1.6rem;
            }
        }
    }

    &__base-price-left {
        &__price-total {
            font-size: 1.50rem;
            font-weight: 700;
            font-family: $headingFont;
            color: #2C2D3D;
            margin-right: 1.22rem;

            @media (max-width: 390px) {
                margin-right: 0;
            }
        }

        &__add-to-cart {
            display: inline-block;
            padding: 16px 37px;
            background: linear-gradient(184.96deg, #489CFF 1.19%, #0075FF 101.54%);
            box-shadow: 0px 3px 6px rgba(31, 141, 237, 0.3);
            border-radius: 4px;
            color: #fff;
            font-family: $paragraphFont;
            font-weight: 600;

            @media (max-width: 390px) {
                margin: .69rem 0 0;
            }

            @media (max-width: 310px) {
                padding: .8rem 1.50rem;
            }
        }
    }

    &__recommended-heading {
        font-weight: 800;
        font-family: $paragraphFont;
        font-size: 2.88rem;
        margin: 3.75rem 0 2.50rem;
        color: $primamryColorBlue;

        @media (max-width: 348px) {
            font-size: 1.88rem;
        }

        @media (max-width: 262px) {
            font-size: 2.3rem;
        }
    }

    &__domain-box {
        display: flex;
        justify-content: space-between;
        box-shadow: 0px 1px 3px rgba(44, 45, 61, 0.16);
        border-radius: 4px;
        padding: 6px 26px;
        margin-bottom: .38rem;

        @media (max-width: 545px) {
            display: block;
        }

        @media (max-width: 367px) {
            display: block;
            text-align: center;
            margin-bottom: 1.25rem;
        }

        &__left {
            display: flex;
            align-items: center;

            @media (max-width: 367px) {
                justify-content: center;
            }

            &__domain-name {
                font-size: 1.13rem;
                color: $primamryColorBlue;
                font-family: $paragraphFont;
                font-weight: 600;
            }
        }

        &__right {
            display: flex;
            justify-content: space-between;

            @media (max-width: 367px) {
                display: block;
            }

            &__price {
                font-size: 1.13rem;
                color: $primamryColorBlue;
                font-family: $paragraphFont;
                font-weight: 600;
                margin-right: 1.25rem;
                display: flex;
                align-items: center;

                @media (max-width: 367px) {
                    justify-content: center;
                    margin: 10px 0px 12px;
                }

                .yearly {
                    color: $primamryColorParagraph;
                    font-size: 0.75rem;
                    font-family: $headingFont;
                    font-weight: 600;
                    margin-left: .25rem;
                }
            }

            &__btn {
                padding: .63rem .88rem;
                background: linear-gradient(184.96deg, #489CFF 1.19%, #0075FF 101.54%);
                box-shadow: 0px 1px 3px rgba(31, 141, 237, 0.3);
                border-radius: 4px;
                color: #fff;
                font-size: 1rem;
                font-family: $paragraphFont;
                font-weight: 600;
                display: inline-block;
            }
        }
    }

    // Footer Bottom
    &__bottom {
        background: $primamryColorBlue;
        margin-top: 100px;
    }

    &__content {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        padding: 35px 0 35px 0;

        @media only screen and (max-width: 600px) {
            padding: 15px 0 20px 0;
        }
    }

    &__copyright {
        font-size: 14px;
        font-weight: 400;
        color: $primamryColorWhite;
        margin: 0;

        @media only screen and (max-width: 600px) {
            display: block;
            margin-left: auto;
            margin-right: auto;
        }
    }

    &__footer-links {
        display: flex;
        list-style: none;
        margin: 0;
        padding: 0;

        li {
            a {
                color: #ffffff;
                font-size: 14px;
                font-weight: 400;
                margin-left: 10px;
                text-decoration: none;

                @media only screen and (max-width: 600px) {
                    margin-right: 10px;
                    margin-left: 0;
                }
            }
        }
    }

    &__links-container {
        @media only screen and (max-width: 600px) {
            display: block;
            margin-left: auto;
            margin-right: auto;
        }
    }
}