.hero {
    &__image {
        background: url("/media/hero/hero-img.png");
        background-position: top center;
        background-repeat: no-repeat;
        background-size: cover;
        margin: 0;
        padding: 0;
        width: 100%;

        @media only screen and (max-width: 600px) {
            height: 90vh;
        }

    }

    &__content {
        color: #ffffff;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        min-height: 100vh;

        @media only screen and (max-width: 600px) {
            min-height: 90vh;
        }
    }

    &__heading {
        @include heroTitle;
        margin-top: 75px;
    }

    &__paragraph {
        @include heroDescription;
        padding: 24px 10px 74px 10px;

        @media only screen and (max-width: 600px) {
            padding: 35px 10px 50px 10px;
        }
    }

    &__get-started-button {
        @include nawabBtnOne;
        padding: 11px 20px;
        margin: 0 2px 0 2px;
    }

    &__contact-us-button {
        @include nawabBtnTwo;
        padding: 9px 20px;
        margin: 0 2px 0 2px;
    }

    &__arrow-icon {
        font-size: 14px;
    }
}

@keyframes introload {
    from {
        clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
    }

    to {
        clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
    }
}