.faq {
    padding: 150px 0 120px 0;

    @media only screen and (max-width: 600px) {
        padding: 50px 0 40px 0;
    }

    &__section-title {
        @include nawabSectionTitle;
    }

    &__section-description {
        @include nawabSectionDescription;
        margin-bottom: 33px;
    }

    &__item {
        background: $primamryColorWhite;
        border: none;
        box-shadow: 0px 1px 3px rgb(44 45 61 / 16%);
    }

    &__button {
        background: none;
        border-radius: 4px;
        margin-bottom: 6px;
        border: none;
        font-weight: 500;
        font-size: 20px;
        color: $primamryColorBlue;

        &:focus {
            box-shadow: none;
            background: none;
            color: black;
        }

        &:not(.collapsed) {
            color: $primamryColorBlue;
            margin-bottom: 0;
            border: none;
            box-shadow: none;
            background: $primamryColorWhite;
        }
    }

    &__faq-body {
        background: #ffffff;
        border-radius: 4px;
        margin-bottom: 5px;
        padding-top: 0;
        font-weight: 400;
        font-size: 16px;
        line-height: 26px;
        color: $primamryColorParagraph;
    }
}

.accordion-collapse.show {
    box-shadow: 0px 20px 20px rgba(44, 45, 61, 0.16);
    z-index: 2;
    position: relative;
}

.accordion-button::after {
    flex-shrink: 0;
    width: 1.25rem;
    height: 1.25rem;
    margin-left: auto;
    background: url("/media/faq/plus.svg");
    content: "";
    background-repeat: no-repeat;
    background-size: 1.25rem;
    transition: transform 0.2s ease-in-out;
}

.accordion-button:not(.collapsed)::after {
    background-image: url("/media/faq/minus.svg");
    transform: rotate(-180deg);
}