.search {
    padding: 20px;
    margin-top: 200px;

    &__wrap {
        max-width: 800px;
        background: $primamryColorWhite;
        box-shadow: 0px 1px 3px rgb(44 45 61 / 16%);
        padding: 3px;
        border-radius: 4px;

        @media only screen and (max-width: 600px) {
            width: 100%;
        }
    }

    &__search {
        width: 100%;
        display: flex;
    }

    &__searchTerm {
        width: 100%;
        border: none;
        outline: none;
        background: none;
        padding-left: 20px;
        font-weight: 500;
        color: $primamryColorParagraph;
        font-size: 16px;
    }

    &__searchButton {
        background: linear-gradient(184.96deg, #489cff 1.19%, #0075ff 101.54%);
        border: none;
        border-radius: 4px;
        text-align: center;
        color: #fff;
        cursor: pointer;
        font-size: 16px;
        padding: 9px 24px;
    }
}